import React from 'react';
import type { MobileBiFactory } from '@ali/4ever-cangjie';
import { moFactory, TemplateButton } from '../mo';

export const mobileBiFactory: MobileBiFactory = () => {
  return {
    ...moFactory({}),
    renderNode(props, _, next) {
      if (!TemplateButton.isTemplateButton(props.node)) return next();
      return <></>;
    },
    models: {
      klass: 'block',
      type: TemplateButton.TYPE,
    },
  };
};
